<template>
  <datepicker v-model="date_value" @change="emitToParent" :formatter="momentFormat" :shortcuts="shortcuts"
              placeholder="Selecione a data"/>
</template>

<script>
import DatePicker from 'vue2-datepicker'
import 'vue2-datepicker/index.css'
import 'vue2-datepicker/locale/pt-br'
import moment from 'moment'

export default {
  name: 'Mydatepicker',
  components: {
    'datepicker': DatePicker
  },
  data () {
    return {
      // date_value: '2020-03-17',
      date_value: new Date(2019, 11, 15), // initial date_value
      childMessage: '',
      shortcuts: [
        {
          text: 'Hoje',
          onClick () {
            const date = new Date()
            return date
          }
        },
        {
          text: 'Ontem',
          onClick () {
            const date = new Date()
            date.setTime(date.getTime() - 3600 * 1000 * 24)
            return date
          }
        }
      ],
      momentFormat: {
        // Date to String
        stringify: (date) => {
          return date ? moment(date).format('DD/MM/YYYY') : ''
        },
        // String to Date
        parse: (value) => {
          return value ? moment(value, 'DD/MM/YYYY').toDate() : null
        }
      }
    }
  },
  beforeMount () {
    this.emitToParent()
  },
  methods: {
    emitToParent () {
      this.$emit('childToParent', moment(this.date_value).format('YYYY-MM-DD'))
    }
  }
}
</script>

